import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { User, Lock, Eye, EyeOff } from 'lucide-react';
import CommonLayout from './CommonLayout';
import "../styles/LoginPage.css"
import { CognitoUser, AuthenticationDetails } from 'amazon-cognito-identity-js';
import { userPool } from '../aws-config';
import { logSessionEvent, decodeToken } from '../utils/tokenUtils';

class LoginPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      username: '',
      password: '',
      showPassword: false,
      isLoading: false,
      error: null
    };
  }

  componentDidMount() {
    // URL     
    const urlParams = new URLSearchParams(window.location.search);
    const reason = urlParams.get('reason');
    
    if (reason === 'session_expired') {
      this.setState({
        error: ' .  .'
      });
    }
  }

  handleInputChange = (e) => {
    const { name, value } = e.target;
    this.setState({ [name]: value, error: null });
  };

  togglePasswordVisibility = () => {
    this.setState(prevState => ({ showPassword: !prevState.showPassword }));
  };

  handleLogin = async (e) => {
    e.preventDefault();
    
    const { username, password } = this.state;
    
    if (!username || !password) {
      this.setState({ error: '    .' });
      return;
    }

    this.setState({ isLoading: true, error: null });

    try {
      // AWS Cognito SDK  
      const user = new CognitoUser({ Username: username, Pool: userPool });
      const authDetails = new AuthenticationDetails({ Username: username, Password: password });

      user.authenticateUser(authDetails, {
        onSuccess: (session) => {
          //  
          const idToken = session.getIdToken().getJwtToken();
          const accessToken = session.getAccessToken().getJwtToken();
          const refreshToken = session.getRefreshToken().getToken();
          
          // JWT     
          const tokenPayload = decodeToken(idToken);
          const actualSub = tokenPayload?.sub || 'cognito_user_' + Date.now();
          const actualEmail = tokenPayload?.email || (username.includes('@') ? username : `${username}@cognito.local`);
          const actualUsername = tokenPayload?.cognito_username || tokenPayload?.username || username;
          
          const userData = {
            username: actualUsername,
            sub: actualSub,
            email: actualEmail,
            access_token: accessToken,
            id_token: idToken,
            refresh_token: refreshToken,
            profile: {
              name: actualUsername,
              username: actualUsername
            }
          };
          
          // sessionStorage   (  )
          const tokens = {
            id_token: userData.id_token,
            access_token: userData.access_token,
            refresh_token: userData.refresh_token,
          };
          
          sessionStorage.setItem('cognitoTokens', JSON.stringify(tokens));
          
          //   sessionStorage  ( )
          const currentUser = {
            username: userData.username,
            sub: userData.sub,
            email: userData.email,
            profile: userData.profile,
            //    (  )
            id_token: userData.id_token,
            access_token: userData.access_token,
            refresh_token: userData.refresh_token,
          };
          sessionStorage.setItem('currentUser', JSON.stringify(currentUser));
          
          logSessionEvent('LOGIN_SUCCESS', { 
            username: userData.username, 
            storage: 'sessionStorage' 
          });
          
          console.log('Cognito  :', userData);
          this.props.onLogin(userData);
          this.props.navigate('/');
        },
        onFailure: (err) => {
          console.error('Cognito  :', err);
          
          const code = err?.code || err?.__type;
          const message = err?.message || '';
          
          // User is disabled     
          if (code === 'NotAuthorizedException' && message.includes('User is disabled')) {
            this.setState({
              error: ' .',
              isLoading: false
            });
            return;
          }
          
          const map = {
            NotAuthorizedException: '     .',
            UserNotConfirmedException: '  .  .',
            UserNotFoundException: '  .',
            PasswordResetRequiredException: '  . " " .',
            NEW_PASSWORD_REQUIRED: '  .  .',
            UserLambdaValidationException: ' .',
            InvalidUserPoolConfigurationException: ' .',
          };
          
          this.setState({
            error: map[code] || err?.message || ' .',
            isLoading: false
          });
        },
        newPasswordRequired: (userAttributes, requiredAttributes) => {
          this.setState({
            error: '   .',
            isLoading: false
          });
        }
      });
    } catch (error) {
      console.error(' :', error);
      this.setState({ 
        error: '    .',
        isLoading: false 
      });
    }
  };

  render() {
    const { username, password, showPassword, isLoading, error } = this.state;

    return (
      <CommonLayout
        isLoggedIn={false}
        currentUser={null}
        navigate={this.props.navigate}
        hideSidebar={true} 
        hideSearch={true}
        onCategoryChange={this.handleCategoryChange}
      >
        <div className="auth-page">
          <div className="auth-container">
            <div className="auth-header">
              <h1 className="auth-title"></h1>
              <p className="auth-subtitle">   .</p>
            </div>

            {error && (
              <div className="error-message">
                {error}
              </div>
            )}

            <form className="auth-form" onSubmit={this.handleLogin}>
              <div className="form-group">
                <label className="form-label">
                  ID
                </label>
                <div className='form-input-container'>
                  <input
                    type="text"
                    name="username"
                    value={username}
                    onChange={this.handleInputChange}
                    className="form-input"
                    placeholder=" "
                    required
                  />
                  <p className='password-toggle'/>
                </div>
              </div>

              <div className="form-group">
                <label className="form-label">
                  Password
                </label>
                <div className="form-input-container">
                  <input
                    type={showPassword ? 'text' : 'password'}
                    name="password"
                    value={password}
                    onChange={this.handleInputChange}
                    className="form-input"
                    placeholder=" "
                    required
                  />
                  <button
                    type="button"
                    className="password-toggle"
                    onClick={this.togglePasswordVisibility}
                  >
                    {showPassword ? <EyeOff size={16} /> : <Eye size={16} />}
                  </button>
                </div>
              </div>

              <button 
                type="submit"
                className="auth-button" 
                disabled={isLoading}
              >
                {isLoading ? ' ...' : ''}
              </button>
            </form>

            <div className="auth-footer">
              <p>
                 ?{' '}
                <button
                  onClick={() => this.props.navigate('/signup')}
                  className="auth-link"
                >
                  
                </button>
              </p>
              <p>
                <button 
                  onClick={() => this.props.navigate('/forgot-password')} 
                  className="auth-link"
                >
                   
                </button>
              </p>
            </div>
          </div>
        </div>
      </CommonLayout>
    );
  }
}

export default LoginPage;
