import React, { Component } from 'react';
import { Mail, Lock, Eye, EyeOff, ArrowLeft, User as UserIcon } from 'lucide-react';
import CommonLayout from './CommonLayout';
import { CognitoUser } from 'amazon-cognito-identity-js';
import { userPool } from '../aws-config';

class ForgotPasswordPage extends Component {
  constructor(props) {
          super(props);
      this.state = {
        // 1 (username + )
        username: '',
        email: '',
 
        // 2 
        confirmationCode: '',
        newPassword: '',
        confirmPassword: '',        
        showNewPassword: false,
        showConfirmPassword: false,
 
        //  
        usernameForCognito: '',           //   username 
        currentStep: 1,                   // 1: , 2: 
        isLoading: false,
        error: null,
        success: null,
      };
  }

  // =====  =====
  handleInputChange = (e) => {
    const { name, value } = e.target;
    this.setState({ [name]: value, error: null, success: null });
  };

  togglePasswordVisibility = (field) =>
    this.setState((prev) => ({ [field]: !prev[field] }));

  validateEmail = (email) => /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email);

  validatePassword = (password) => {
    const minLength = 8;
    const hasLowerCase = /[a-z]/.test(password);
    const hasNumbers = /\d/.test(password);
    const hasSpecialChar = /[^A-Za-z0-9]/.test(password);
    return password.length >= minLength && hasLowerCase && hasNumbers && hasSpecialChar;
  };

  /**
   * ()  username+email   API   
   * .env: REACT_APP_USERNAME_EMAIL_VERIFY_URL (POST, body: { username, email })
   *  : { "valid": true }
   */
  verifyUsernameEmail = async (username, email) => {
    const base = process.env.REACT_APP_USERNAME_EMAIL_VERIFY_URL;
    if (!base) return true; //     
    try {
      const res = await fetch(base, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ username, email })
      });
      if (!res.ok) return false;
      const data = await res.json().catch(() => ({}));
      return data?.valid !== false;
    } catch {
      return false;
    }
  };

  // ===== 1:   =====
  requestResetCode = async (e) => {
    e.preventDefault();
    const { username, email } = this.state;

    if (!username) return this.setState({ error: ' .' });
    if (!email) return this.setState({ error: ' .' });
    if (!this.validateEmail(email)) return this.setState({ error: '   .' });

    //    
    console.log('   :', {
      username,
      email,
      userPool: userPool.getUserPoolId(),
      clientId: userPool.getClientId()
    });

    this.setState({ isLoading: true, error: null, success: null });

    try {
      // ()  username+email  
      const verified = await this.verifyUsernameEmail(username, email);
      if (!verified) {
        this.setState({ isLoading: false, error: '    .' });
        return;
      }

      // 2) Cognito forgotPassword 
      const user = new CognitoUser({ Username: username, Pool: userPool });
      user.forgotPassword({
        onSuccess: () => {
          this.setState({
            isLoading: false,
            currentStep: 2,
            success: '    .',
            usernameForCognito: username,
          });
        },
        onFailure: (err) => {
          console.error('forgotPassword error', err);
          
          //     
          console.log('Error details:', {
            code: err?.code,
            name: err?.name,
            message: err?.message,
            stack: err?.stack
          });
          
          const map = {
            UserNotFoundException: '    .',
            CodeDeliveryFailureException: '  . Cognito  .',
            LimitExceededException: '  .    .',
            InvalidParameterException: '() /  .',
            NotAuthorizedException: '  .     .',
          };
          
          const msg = map[err?.code] || err?.message || '  .';
          this.setState({ isLoading: false, error: msg });
        },
        inputVerificationCode: () => {
          this.setState({
            isLoading: false,
            currentStep: 2,
            usernameForCognito: username,
          });
        },
      });
    } catch (e1) {
      console.error(e1);
      this.setState({ isLoading: false, error: '    .' });
    }
  };

  // ===== 2:  +    =====
  confirmNewPassword = async (e) => {
    e.preventDefault();
    const { confirmationCode, newPassword, confirmPassword, usernameForCognito } = this.state;

    if (!confirmationCode || !newPassword || !confirmPassword)
      return this.setState({ error: '    .' });

    if (newPassword !== confirmPassword)
      return this.setState({ error: '  .' });

    if (!this.validatePassword(newPassword))
      return this.setState({
        error: '  8 , , ,   .',
      });

    // 1  usernameForCognito  (     )
    const username = usernameForCognito;
    const user = new CognitoUser({ Username: username, Pool: userPool });

    this.setState({ isLoading: true, error: null, success: null });
    user.confirmPassword(confirmationCode, newPassword, {
      onSuccess: () => {
        this.setState({ isLoading: false, success: ' . .' });
        this.props.navigate('/login');
      },
      onFailure: (err) => {
        console.error('confirmPassword error', err);
        
        //     
        console.log('confirmPassword error details:', {
          code: err?.code,
          name: err?.name,
          message: err?.message,
          stack: err?.stack
        });
        
        const map = {
          ExpiredCodeException: '  .   .',
          CodeMismatchException: '   .  .',
          InvalidPasswordException: ' Cognito   .  .',
          LimitExceededException: '  .    .',
        };
        
        const msg = map[err?.code] || err?.message || '  .';
        this.setState({ isLoading: false, error: msg });
      },
    });
  };

  // =====  =====
  goBackToStep1 = () => {
    this.setState({
      currentStep: 1,
      username: '',
      confirmationCode: '',
      newPassword: '',
      confirmPassword: '',
      error: null,
      success: null,
    });
  };

  // ===== UI =====
  renderStep1() {
    const { username, email, isLoading, error } = this.state;

    return (
      <div className="auth-container">
        <div className="auth-header">
          <h1 className="auth-title"> </h1>
          <p className="auth-subtitle">  .</p>
        </div>

        {error && <div className="error-message">{error}</div>}

        <form className="auth-form" onSubmit={this.requestResetCode}>
          <div className="form-group">
            <label className="form-label">
              <UserIcon size={16} />
              ID
            </label>
            <input
              type="text"
              name="username"
              value={username}
              onChange={this.handleInputChange}
              className="form-input"
              placeholder="ID"
              required
            />
          </div>
          <div className="form-group">
            <label className="form-label">
               
            </label>
            <input
              type="email"
              name="email"
              value={email}
              onChange={this.handleInputChange}
              className="form-input"
              placeholder="example@email.com"
              required
            />
          </div>

          <button type="submit" className="auth-button" disabled={isLoading}>
            {isLoading ? ' ...' : '  '}
          </button>
        </form>

        <div className="auth-footer">
          <button onClick={() => this.props.navigate('/login')} className="auth-link">
            <ArrowLeft size={16} /> 
          </button>
        </div>
      </div>
    );
  }

  renderStep2() {
    const {
      confirmationCode,
      newPassword,
      confirmPassword,
      showNewPassword,
      showConfirmPassword,
      isLoading,
      error,
      success,
    } = this.state;

    return (
      <div className="auth-container">
        <div className="auth-header">
          <h1 className="auth-title">  </h1>
          <p className="auth-subtitle">{email}     <br/>  .</p>
        </div>

        {error && <div className="error-message">{error}</div>}
        {success && <div className="success-message">{success}</div>}

        <form className="auth-form" onSubmit={this.confirmNewPassword}>
          <div className="form-group">
            <label className="form-label"> </label>
            <input
              type="text"
              name="confirmationCode"
              value={confirmationCode}
              onChange={this.handleInputChange}
              className="form-input"
              placeholder="6   "
              maxLength="6"
              required
            />
          </div>

          <div className="form-group">
            <label className="form-label">
               
            </label>
            <div className="form-input-container">
              <input
                type={showNewPassword ? 'text' : 'password'}
                name="newPassword"
                value={newPassword}
                onChange={this.handleInputChange}
                className="form-input"
                placeholder="  "
                required
              />
              <button
                type="button"
                className="password-toggle"
                onClick={() => this.togglePasswordVisibility('showNewPassword')}
              >
                {showNewPassword ? <EyeOff size={16} /> : <Eye size={16} />}
              </button>
            </div>
            <small className="form-help"> 8 , , ,  </small>
          </div>

          <div className="form-group">
            <label className="form-label">
                
            </label>
            <div className="form-input-container">
              <input
                type={showConfirmPassword ? 'text' : 'password'}
                name="confirmPassword"
                value={confirmPassword}
                onChange={this.handleInputChange}
                className="form-input"
                placeholder="   "
                required
              />
              <button
                type="button"
                className="password-toggle"
                onClick={() => this.togglePasswordVisibility('showConfirmPassword')}
              >
                {showConfirmPassword ? <EyeOff size={16} /> : <Eye size={16} />}
              </button>
            </div>
          </div>

          <button type="submit" className="auth-button" disabled={isLoading}>
            {isLoading ? ' ...' : ' '}
          </button>
        </form>

        <div className="auth-footer">
          <button onClick={this.goBackToStep1} className="auth-link">
            <ArrowLeft size={16} />
              
          </button>
        </div>
      </div>
    );
  }

  render() {
    return (
      <CommonLayout 
        isLoggedIn={false} 
        currentUser={null} 
        navigate={this.props.navigate}
        hideSidebar={true} 
        hideSearch={true}
      >
        <div className="auth-page">
          {this.state.currentStep === 1 ? this.renderStep1() : this.renderStep2()}
        </div>
      </CommonLayout>
    );
  }
}

export default ForgotPasswordPage;
