import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { User, ArrowLeft, Save, X } from 'lucide-react';
import CommonLayout from './CommonLayout';
import "../styles/WritePostPage.css"
import { decodeToken, getCognitoToken } from '../utils/tokenUtils';

class WritePostPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      postId: null,
      title: "",
      content: "",
      category: "",
      isLoading: false,
      error: null
    };
    this.categories = ["", "/", "", "/", ""];
  }

  componentDidMount() {
    const urlParams = new URLSearchParams(window.location.search);
    const postId = urlParams.get('edit'); // CHANGED TO 'edit'
    
    if (postId) {
      this.setState({ postId });
      this.fetchPostForEdit(postId);
    }
  }

  componentDidUpdate(prevProps) {
    const urlParams = new URLSearchParams(window.location.search);
    const newPostId = urlParams.get('edit'); // CHANGED TO 'edit'
    
    if (newPostId !== this.state.postId) {
      if (newPostId) {
        this.setState({ postId: newPostId });
        this.fetchPostForEdit(newPostId);
      } else {
        this.setState({ postId: null, title: "", content: "", category: "" });
      }
    }
  }
  
  fetchPostForEdit = async (postId) => {
    this.setState({ isLoading: true });
    try {
      const response = await fetch(`http://localhost:8081/api/v1/posts/${postId}`);
      if (!response.ok) {
        throw new Error('  .');
      }
      const data = await response.json();
      const post = data.post || data.data;
      if (post) {
        this.setState({
          title: post.title,
          content: post.content,
          category: post.category,
          isLoading: false
        });
      }
    } catch (error) {
      console.error('  :', error);
      this.setState({
        error: error.message,
        isLoading: false
      });
    }
  };


  handleInputChange = (field, value) => {
    this.setState({ [field]: value });
  };

  handleSubmit = async (e) => {
    e.preventDefault();
    
    //   
    if (!this.state.title.trim()) {
      alert("  .");
      return;
    }

    if (!this.state.content.trim()) {
      alert("  .");
      return;
    }

    if (this.state.content.trim().length < 5) {
      alert("  5  .");
      return;
    }

    this.setState({ isLoading: true, error: null });

    const { title, content, category, postId } = this.state;
    const { currentUser } = this.props;

    // JWT     
    const token = getCognitoToken();
    const tokenPayload = token ? decodeToken(token) : null;
    const actualSub = tokenPayload?.sub || currentUser?.sub;
    const actualUsername = tokenPayload?.cognito_username || tokenPayload?.username || currentUser?.username || 'Guest';

    const postData = {
      title,
      content,
      category,
      // Add these fields for post creation/update
      user_id: actualSub,
      username: actualUsername,
    };

    let url = `http://localhost:8081/api/v1/posts`;
    let method = 'POST';

    if (postId) {
      // If postId exists, it's an update operation
      url = `http://localhost:8081/api/v1/posts/${postId}`;
      method = 'PATCH';
    }
    try {
      // JWT  
      const token = getCognitoToken();
      console.log(' WritePostPage -  :', {
        hasToken: !!token,
        tokenLength: token ? token.length : 0,
        tokenPreview: token ? token.substring(0, 20) + '...' : 'null'
      });
      
      const headers = {
        'Content-Type': 'application/json'
      };
      
      //   Authorization  
      if (token) {
        headers['Authorization'] = `Bearer ${token}`;
        console.log(' WritePostPage - Authorization  ');
      } else {
        console.warn(' WritePostPage -  !');
      }
      
      console.log(' WritePostPage -  :', headers);

      const response = await fetch(url, {
        method: method,
        headers: headers,
        body: JSON.stringify(postData),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.message || '  .');
      }

      const result = await response.json();
      console.log("  :", result);
      alert(postId ? "  ." : "  .");
      
      // Navigate back to the main board page
      this.props.navigate('/');

    } catch (error) {
      console.error('  :', error);
      this.setState({ error: error.message });
    } finally {
      this.setState({ isLoading: false });
    }
  };

  handleCancel = () => {
    if (this.state.title.trim() || this.state.content.trim()) {
      if (window.confirm('   .  ?')) {
        this.props.navigate('/');
      }
    } else {
      this.props.navigate('/');
    }
  };

  render() {
    const { isLoggedIn, currentUser, profileImage } = this.props;
    const { postId, title, content, category, isLoading, error } = this.state;

    //   
    console.log('WritePostPage :', { isLoggedIn, currentUser });
    console.log('  :', currentUser?.id_token);

    if (!isLoggedIn) {
      return (
        <CommonLayout
          isLoggedIn={isLoggedIn}
          currentUser={currentUser}
          navigate={this.props.navigate}
        >
          <div style={{ 
            textAlign: 'center', 
            padding: '60px 20px',
            color: 'var(--muted-foreground)'
          }}>
            <h2 style={{ 
              fontSize: '24px', 
              marginBottom: '16px',
              color: 'var(--foreground)'
            }}>
               
            </h2>
            <p style={{ 
              fontSize: '16px', 
              marginBottom: '24px',
              lineHeight: '1.6'
            }}>
                 .
            </p>
            <button
              onClick={() => this.props.navigate('/login')}
              style={{
                padding: '12px 24px',
                backgroundColor: 'var(--primary)',
                color: 'var(--primary-foreground)',
                border: 'none',
                borderRadius: 'var(--radius)',
                fontSize: '16px',
                cursor: 'pointer',
                transition: 'all 0.2s ease'
              }}
              onMouseEnter={(e) => {
                e.target.style.opacity = '0.9';
              }}
              onMouseLeave={(e) => {
                e.target.style.opacity = '1';
              }}
            >
              
            </button>
          </div>
        </CommonLayout>
      );
    }

    return (
      <CommonLayout
        isLoggedIn={isLoggedIn}
        currentUser={currentUser}
        navigate={this.props.navigate}
        hideSearch={true}
        onCategoryChange={this.handleCategoryChange}
        onLogout={this.props.onLogout}
      >
        <div className="write-post-container">
          {/*   */}
          <div className="back-button-container">
            <button
              onClick={this.handleCancel}
              className="back-button"
            >
              <ArrowLeft size={20} />
              
            </button>
          </div>

          <div className="write-post-header">
            <h1 className="write-post-title">{postId ? ' ' : '  '}</h1>
            <div className="write-post-actions">
              <button
                type="button"
                className="cancel-btn"
                onClick={this.handleCancel}
                disabled={isLoading}
              >
                <X size={16} />
                
              </button>
              <button
                type="submit"
                className="save-btn"
                onClick={this.handleSubmit}
                disabled={isLoading}
              >
                <Save size={16} />
                {isLoading ? ' ...' : ''}
              </button>
            </div>
          </div>

          {error && (
            <div className="error-message">
              <p>: {error}</p>
            </div>
          )}

          <form className="write-post-form" onSubmit={this.handleSubmit}>
            <div className="post-form-group">
              <label htmlFor="title" className="post-form-label"></label>
              <input
                type="text"
                id="title"
                className="write-form-input"
                value={title}
                onChange={(e) => this.handleInputChange('title', e.target.value)}
                placeholder="  "
                maxLength={100}
                required
              />
              <span className="post-char-count">{title.length}/100</span>
            </div>

            <div className="post-form-group">
              <label htmlFor="category" className="post-form-label"></label>
              <div className="category-input-container"> {/*    () */}
                <select
                  id="category"
                  className={`post-form-select ${postId ? 'disabled' : ''}`}
                  value={category}
                  onChange={(e) => this.handleInputChange('category', e.target.value)}
                  disabled={!!postId}
                  required
                >
                  {this.categories.map((cat) => (
                    <option key={cat} value={cat}>
                      {cat}
                    </option>
                  ))}
                </select>
                {postId && <span className="disabled-hint">(     )</span>}
              </div>
            </div>

            <div className="post-form-group">
              <label htmlFor="content" className="post-form-label"></label>
              <textarea
                id="content"
                className="post-form-textarea"
                value={content}
                onChange={(e) => this.handleInputChange('content', e.target.value)}
                placeholder="   ( 5)"
                rows={15}
                minLength={5}
                required
              />
              <span className="post-char-count">{content.length}</span>
            </div>
          </form>
        </div>
      </CommonLayout>
    );
  }
}

export default WritePostPage;
