class UserService {
  constructor() {
    this.baseURL = process.env.REACT_APP_USER_SERVICE_URL || 'http://localhost:8081';
  }

  //   
  getAuthHeaders() {
    //    
    const { createAuthHeaders } = require('../utils/tokenUtils');
    return createAuthHeaders();
  }

  // 
  async register(userData) {
    try {
      const response = await fetch(`${this.baseURL}/auth/register`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(userData)
      });

      if (!response.ok) {
        throw new Error(` : ${response.status}`);
      }

      return await response.json();
    } catch (error) {
      console.error(' :', error);
      throw error;
    }
  }

  //  ( API )
  async login(credentials) {
    try {
      const response = await fetch(`${this.baseURL}/auth/login`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(credentials)
      });

      if (!response.ok) {
        //     
        let errorMessage = ` : ${response.status}`;
        try {
          const errorData = await response.json();
          if (errorData?.message) {
            errorMessage = errorData.message;
          }
        } catch (parseError) {
          console.warn('   :', parseError);
        }
        throw new Error(errorMessage);
      }

      const data = await response.json();
      
      //   
      if (data.accessToken) {
        sessionStorage.setItem('backendAccessToken', data.accessToken);
      }

      return data;
    } catch (error) {
      console.error(' :', error);
      throw error;
    }
  }

  // 
  async logout() {
    try {
      const response = await fetch(`${this.baseURL}/auth/logout`, {
        method: 'POST',
        headers: this.getAuthHeaders()
      });

      if (response.ok) {
        sessionStorage.removeItem('backendAccessToken');
      }

      return response.ok;
    } catch (error) {
      console.error(' :', error);
      throw error;
    }
  }

  //   
  async getMyProfile() {
    try {
      const response = await fetch(`${this.baseURL}/users/me`, {
        method: 'GET',
        headers: this.getAuthHeaders()
      });

      if (!response.ok) {
        throw new Error(`  : ${response.status}`);
      }

      return await response.json();
    } catch (error) {
      console.error('  :', error);
      throw error;
    }
  }

  //  
  async updateProfile(profileData) {
    try {
      const response = await fetch(`${this.baseURL}/users/me`, {
        method: 'PATCH',
        headers: this.getAuthHeaders(),
        body: JSON.stringify(profileData)
      });

      if (!response.ok) {
        throw new Error(`  : ${response.status}`);
      }

      return await response.json();
    } catch (error) {
      console.error('  :', error);
      throw error;
    }
  }

  //  
  async changePassword(passwordData) {
    try {
      const response = await fetch(`${this.baseURL}/users/me`, {
        method: 'PATCH',
        headers: this.getAuthHeaders(),
        body: JSON.stringify(passwordData)
      });

      if (!response.ok) {
        throw new Error(`  : ${response.status}`);
      }

      return await response.json();
    } catch (error) {
      console.error('  :', error);
      throw error;
    }
  }

  //  
  async deleteAccount() {
    try {
      const response = await fetch(`${this.baseURL}/users/me`, {
        method: 'DELETE',
        headers: this.getAuthHeaders()
      });

      if (response.ok) {
        sessionStorage.removeItem('backendAccessToken');
      }

      return response.ok;
    } catch (error) {
      console.error('  :', error);
      throw error;
    }
  }

  //  ()
  async deactivateAccount() {
    try {
      const url = `${this.baseURL}/api/v1/users/me/deactivate`;
      console.log('[Deactivate] request URL:', url);
      const response = await fetch(url, {
        method: 'POST',
        headers: this.getAuthHeaders(),
        credentials: 'include'
      });

      if (response.ok) {
        sessionStorage.removeItem('backendAccessToken');
      }

      if (!response.ok) {
        //     
        let message = `  : ${response.status}`;
        try {
          const data = await response.json();
          if (data?.message) message = data.message;
        } catch (_) {}
        throw new Error(message);
      }

      return true;
    } catch (error) {
      // /CORS/   fetch   
      if (error?.name === 'TypeError') {
        console.error('   :', error);
        throw new Error('   .   CORS  .');
      }
      console.error('  :', error);
      throw error;
    }
  }
}

export default new UserService();
